:root {
  --g: 0 0 4px var(--textc);
  --g2: 0 0 1.25px var(--glow);
  --bsg: -4px -4px 3.5px var(--glow);
  --bsg1: 0 0 10px 1px var(--glow);
  --bsg2: 4px 4px 3.5px var(--glow);
  --bsg3: 0 -5px 5px var(--textc);
  --bsg4: 0px 5px 3px 2px var(--textc);
  --clip-top-left: polygon(-50% -50%, 100% 0, 100% 100%, 0% 100%);
}

